import React, {useEffect} from 'react';
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import ToolItem from "./ToolItem";
import {changeCurrentAdminMenuLink, metaTitle} from "../../../utils/misc";
import useTranslation from "../../../hooks/useTranslation";

const Tools = () => {

    const items = [
        {
            name: useTranslation("API dashboard"),
            description: useTranslation("From this dashboard you can discover and manage every API endpoints exposed by ACPT. Use the playground to learn how to use every endpoint."),
            icon: "icon-park-outline:api",
            link: "api"
        },
        {
            name: useTranslation("Export data"),
            description: useTranslation("Use this tool to quick export the general settings and meta fields for every element in a compressed JSON format called .acpt."),
            icon: "mdi:database-export-outline",
            link: "export"
        },
        {
            name: useTranslation("Import data"),
            description: useTranslation("This tool allows you to import settings and meta fields from previously exported .acpt files (max size allowed is 2Mb)."),
            icon: "mdi:database-import-outline",
            link: "import"
        },
    ];

    useEffect(() => {
        metaTitle(useTranslation("ACPT Tools"));
        changeCurrentAdminMenuLink('#/tools');
    }, []);

    return (
        <Layout>
            <ActionsBar
                title={useTranslation("ACPT Tools")}
            />
            <main>
                <Breadcrumbs crumbs={[
                    {
                        label: useTranslation("Registered Custom Post Types"),
                        link: "/"
                    },
                    {
                        label: useTranslation("ACPT Tools")
                    }
                ]} />
                <div className="acpt-tools-wrapper">
                    {items.map((item)=>(
                        <ToolItem
                            name={item.name}
                            description={item.description}
                            icon={item.icon}
                            link={item.link}
                        />
                    ))}
                </div>
            </main>
        </Layout>
    );
};

export default Tools;